import './Cell.css';
import { forwardRef } from 'react';
import LoadoutCard from '../LoadoutCard/LoadoutCard';
import { motion } from 'framer-motion';

const Cell = forwardRef(
  (
    {
      index,
      onDragStart,
      onDragEnd,
      activeIndex,
      onTapStart,
      clickListener,
      dragConstraints,
      cardName,
    },
    ref
  ) => {
    return (
      <div
        id={index}
        ref={ref}
        className='loadout-space'
        style={activeIndex === index ? { zIndex: 2 } : { zIndex: 1 }}
      >
        {cardName ? (
          <motion.div
            drag
            onDragStart={onDragStart}
            onDragEnd={onDragEnd}
            dragConstraints={dragConstraints}
            onTapStart={onTapStart}
            onClick={() =>
              clickListener
                ? clickListener({ name: cardName, cardInfo: {} })
                : true
            }
            whileTap={{ scale: 1.2 }}
            layout
          >
            <LoadoutCard name={cardName} dragConstraints={dragConstraints} />
          </motion.div>
        ) : (
          <div className='card' />
        )}
      </div>
    );
  }
);

export default Cell;
